import { useCallback, useContext, useEffect, useRef } from 'react';
import mermaid from 'mermaid';

import { ThemeToggleContext } from '@/contexts/ThemeToggleContext.ts';

export const useMermaid = (html: string) => {
  const contentRef = useRef<HTMLDivElement>(null);
  const { theme } = useContext(ThemeToggleContext);

  const renderMermaidDiagrams = useCallback(async () => {
    if (!contentRef.current) return;

    // 테마 변경 시 원본 HTML로 복원
    contentRef.current.innerHTML = html;

    const mermaidElements = contentRef.current.querySelectorAll('code.language-mermaid');

    if (mermaidElements.length === 0) return;

    const mermaidTheme = theme === 'dark' ? 'dark' : 'default';

    mermaid.initialize({
      startOnLoad: false,
      theme: mermaidTheme,
      securityLevel: 'loose',
      darkMode: theme === 'dark',
      themeVariables:
        theme === 'light'
          ? {
              primaryColor: '#ffffff',
              primaryTextColor: '#000000',
              primaryBorderColor: '#000000',
              lineColor: '#000000',
              secondaryColor: '#f8f9fa',
              tertiaryColor: '#ffffff',
              background: '#ffffff',
              mainBkg: '#ffffff',
              secondBkg: '#f8f9fa',
              tertiaryBkg: '#ffffff',
              secondaryTextColor: '#000000',
              tertiaryTextColor: '#000000',
              textColor: '#000000',
              loopTextColor: '#000000',
              noteTextColor: '#000000',
              activationTextColor: '#000000',
              signalTextColor: '#000000',
              labelTextColor: '#000000',
              actorTextColor: '#000000',
              messageLine0: '#000000',
              messageLine1: '#000000',
              messageText: '#000000',
              labelColor: '#000000',
              labelBackground: '#ffffff',
              cScale0: '#ffffff',
              cScale1: '#f8f9fa',
              cScale2: '#e9ecef',
            }
          : {
              primaryColor: '#1a1a1a',
              primaryTextColor: '#ffffff',
              primaryBorderColor: '#ffffff',
              lineColor: '#ffffff',
              secondaryColor: '#2d2d2d',
              tertiaryColor: '#3a3a3a',
              background: '#0f0f0f',
              mainBkg: '#1a1a1a',
              secondBkg: '#2d2d2d',
              tertiaryBkg: '#3a3a3a',
              secondaryTextColor: '#ffffff',
              tertiaryTextColor: '#ffffff',
              textColor: '#ffffff',
              loopTextColor: '#ffffff',
              noteTextColor: '#ffffff',
              activationTextColor: '#ffffff',
              signalTextColor: '#ffffff',
              labelTextColor: '#ffffff',
              actorTextColor: '#ffffff',
              messageLine0: '#ffffff',
              messageLine1: '#ffffff',
              messageText: '#ffffff',
              labelColor: '#ffffff',
              labelBackground: '#1a1a1a',
              cScale0: '#1a1a1a',
              cScale1: '#2d2d2d',
              cScale2: '#3a3a3a',
            },
    });

    // 순차적으로 렌더링 (동시 렌더링 시 ID 충돌 방지)
    for (let index = 0; index < mermaidElements.length; index++) {
      const element = mermaidElements[index];
      const code = element.textContent || '';
      const id = `mermaid-${theme}-${Date.now()}-${index}`;

      try {
        const result = await mermaid.render(id, code);

        const wrapper = document.createElement('div');
        wrapper.innerHTML = result.svg;
        wrapper.className = 'mermaid-diagram';
        wrapper.style.textAlign = 'center';
        wrapper.style.margin = '1rem 0';

        if (element.parentElement) {
          element.parentElement.replaceWith(wrapper);
        }
      } catch (error) {
        console.error('Mermaid rendering error:', error);
      }
    }
  }, [theme, html]);

  useEffect(() => {
    renderMermaidDiagrams();
  }, [html, theme, renderMermaidDiagrams]);

  return { contentRef };
};
